import React from 'react';
import {
    Navbar,
    Nav,
    Container,
    Row,
    Col,
    Jumbotron,
    Button,
    ButtonToolbar,
    Card,
    CardDeck,
    ListGroup,
    Tooltip,
    OverlayTrigger,
    Pagination
} from 'react-bootstrap';
import {Link as ScrollLink, Element, Events} from 'react-scroll'
import {Document, Page, pdfjs} from 'react-pdf';
import Mailto from 'react-protected-mailto'

import personal from './PDFS/Personal_Doc.pdf';
import resume from './PDFS/Carson_Badger_Resume_8_20_2019.pdf';
import compensatoryPickAnalysis from './PDFS/Compensatory_Pick_Analysis_Badger.pdf';
import idisPresenetation from './PDFS/IDIS_3802_Presentation_Badger.pdf';
import nbaData from './PDFS/NBA_Data_Analysis_Project_Badger.pdf';
import nflAnalytics from './PDFS/NFL_Predictive_Analytics_Summary_of_Results_and_Application.pdf';
import swingingForFences from './PDFS/Swinging_for_the_Fences_Badger.pdf';
import data from './data';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;


class App extends React.Component {
    constructor(props) {
        super(props);
        App.scrollToTop = App.scrollToTop.bind(this);
        this.state = {
            personalPage: 1,
            pageNumber: 1,
            renderResume: "hide",
            aboutMeShorten: "auto",
            aboutMeExtended: "hide",
            projectDocuments: ["hide", "hide", "hide", "hide", "hide"],
            projectDesc: ["auto", "auto", "auto", "auto", "auto"],
            projectPageNums: [1, 1, 1, 1, 1],
            projectPageNumsMax: [4, 7, 2, 2, 48],
            projectCards: ["small", "small", "small", "small", "small"],
            jumboClass: "red"
        };

        this.viewResume = this.viewResume.bind(this);
        this.viewAboutMe = this.viewAboutMe.bind(this);
        this.renderAboutMePage = this.renderAboutMePage.bind(this);
    }

    static scrollToTop() {
        App.scrollToTop();
    }

    static scrollTo() {
        App.scrollTo('scroll-to-element', {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart'
        })
    }

    scrollToWithContainer() {
        let goToContainer = new Promise((resolve, reject) => {
            Events.scrollEvent.register('end', () => {
                resolve();
                Events.scrollEvent.remove('end');
            });
            App.scrollTo('scroll-container', {
                duration: 800,
                delay: 0,
                smooth: 'easeInOutQuart'
            });
        });
        goToContainer.then(() =>
            App.scrollTo('scroll-container-second-element', {
                duration: 800,
                delay: 0,
                smooth: 'easeInOutQuart',
                containerId: 'scroll-container'
            }));
    }

    componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
    }

    viewResume() {
        let newRender = "";
        if (this.state.renderResume === "render")
            newRender = "hide";
        else
            newRender = "render";
        this.setState(state => ({
            renderResume: newRender
        }));
    }

    viewAboutMe() {
        if (this.state.aboutMeShorten === "auto") {
            this.setState(state => ({
                aboutMeShorten: "hide",
                aboutMeExtended: "auto"
            }));
        } else {
            this.setState(state => ({
                aboutMeShorten: "auto",
                aboutMeExtended: "hide"
            }));
        }
    }

    renderHeaders() {
        return data.header.map(header => {
            return (
                <ScrollLink key={header.class} activeClass="active" className="{header.class} nav-link"
                            to={header.class}
                            spy={true}
                            smooth={true} duration={500}>
                    {header.title}
                </ScrollLink>
            )
        });
    }

    renderProjectDocument = page => event => {
        let docs = this.state.projectDocuments;
        let descs = this.state.projectDesc;
        let cards = this.state.projectCards;

        if (docs[page] === "hide") {
            docs[page] = "auto";
            cards[page] = "full";
        } else {
            docs[page] = "hide";
            cards[page] = "small";
        }

        if (descs[page] === "hide")
            descs[page] = "auto";
        else
            descs[page] = "hide";


        this.setState(state => ({
            projectDocuments: docs,
            projectDesc: descs,
            projectCards: cards
        }));

    };

    renderProjectPage = (page, direction) => event => {
        let pages = this.state.projectPageNums;
        if (direction === "next") {
            if (this.state.projectPageNums[page] === this.state.projectPageNumsMax[page])
                pages[page] = 1;
            else
                pages[page] = pages[page] + 1;
        } else {
            if (this.state.projectPageNums[page] === 1)
                pages[page] = this.state.projectPageNumsMax[page];
            else
                pages[page] = pages[page] - 1;
        }

        this.setState(state => ({
            projectPageNums: pages
        }));
    };

    renderAboutMePage = (direction) => event => {
        let pages = this.state.personalPage;
        if (direction === "next") {
            if (this.state.personalPage === 5)
                pages = 1;
            else
                pages = pages + 1;
        } else {
            if (this.state.personalPage === 1)
                pages = 5;
            else
                pages = pages - 1;
        }

        this.setState(state => ({
            personalPage: pages
        }));
    };

    renderProjects() {
        return (
            <CardDeck>
                <Card key="4" className={this.state.projectCards[3]}>
                    <Card.Header>NFL Predictive Analytics - Summary and Application</Card.Header>
                    <Card.Body>
                        <Document
                            file={nflAnalytics}
                            loading="Loading..."
                            className={this.state.projectDocuments[3]}
                        >
                            <Page pageNumber={this.state.projectPageNums[3]}/>
                        </Document>
                        <Pagination className={this.state.projectDocuments[3]}>
                            <Pagination.Prev onClick={this.renderProjectPage(3, "prev")}/>
                            <Pagination.Next onClick={this.renderProjectPage(3, "next")}/>
                        </Pagination>
                        <Card.Text className={this.state.projectDesc[3]}>
                            Ran 10-year regression models of what factors determine NFL player success based on position
                            group.
                        </Card.Text>
                    </Card.Body>
                    <div onClick={this.renderProjectDocument(3)} className="projectButton">
                        <Card.Footer>
                            Expand Project
                        </Card.Footer>
                    </div>
                    <Card.Link href={nflAnalytics}>
                        <Card.Footer>
                            View Project in New Tab
                        </Card.Footer>
                    </Card.Link>
                </Card>

                <Card key="2" className={this.state.projectCards[1]}>
                    <Card.Header>49ers Play by Play Analysis</Card.Header>
                    <Card.Body>
                        <Document
                            file={idisPresenetation}
                            loading="Loading..."
                            className={this.state.projectDocuments[1]}
                        >
                            <Page pageNumber={this.state.projectPageNums[1]}/>
                        </Document>
                        <Pagination className={this.state.projectDocuments[1]}>
                            <Pagination.Prev onClick={this.renderProjectPage(1, "prev")}/>
                            <Pagination.Next onClick={this.renderProjectPage(1, "next")}/>
                        </Pagination>
                        <Card.Text className={this.state.projectDesc[1]}>
                            Developed in-game recommendations based on statistical analysis and success metrics.
                        </Card.Text>
                    </Card.Body>
                    <div onClick={this.renderProjectDocument(1)} className="projectButton">
                        <Card.Footer>
                            Expand Project
                        </Card.Footer>
                    </div>
                    <Card.Link href={idisPresenetation}>
                        <Card.Footer>
                            View Project in New Tab
                        </Card.Footer>
                    </Card.Link>
                </Card>

                <Card key="1" className={this.state.projectCards[0]}>
                    <Card.Header>Compensatory Pick Analysis</Card.Header>
                    <Card.Body>
                        <Document
                            file={compensatoryPickAnalysis}
                            loading="Loading..."
                            className={this.state.projectDocuments[0]}
                        >
                            <Page pageNumber={this.state.projectPageNums[0]}/>
                        </Document>
                        <Pagination className={this.state.projectDocuments[0]}>
                            <Pagination.Prev onClick={this.renderProjectPage(0, "prev")}/>
                            <Pagination.Next onClick={this.renderProjectPage(0, "next")}/>
                        </Pagination>
                        <Card.Text className={this.state.projectDesc[0]}>
                            Relied on data insight to determine how NFL teams can strategically manipulate the
                            compensatory pick process. Ran two examples of what players a team should acquire in 2018.
                        </Card.Text>
                    </Card.Body>
                    <div onClick={this.renderProjectDocument(0)} className="projectButton">
                        <Card.Footer>
                            Expand Project
                        </Card.Footer>
                    </div>
                    <Card.Link href={compensatoryPickAnalysis}>
                        <Card.Footer>
                            View Project in New Tab
                        </Card.Footer>
                    </Card.Link>
                </Card>

                <Card key="3" className={this.state.projectCards[2]}>
                    <Card.Header>NBA Wins Analysis</Card.Header>
                    <Card.Body>
                        <Document
                            file={nbaData}
                            loading="Loading..."
                            className={this.state.projectDocuments[2]}
                        >
                            <Page pageNumber={this.state.projectPageNums[2]}/>
                        </Document>
                        <Pagination className={this.state.projectDocuments[2]}>
                            <Pagination.Prev onClick={this.renderProjectPage(2, "prev")}/>
                            <Pagination.Next onClick={this.renderProjectPage(2, "next")}/>
                        </Pagination>
                        <Card.Text className={this.state.projectDesc[2]}>
                            Created models to help determine most significant categories for success in the NBA.
                        </Card.Text>
                    </Card.Body>
                    <div onClick={this.renderProjectDocument(2)} className="projectButton">
                        <Card.Footer>
                            Expand Project
                        </Card.Footer>
                    </div>
                    <Card.Link href={nbaData}>
                        <Card.Footer>
                            View Project in New Tab
                        </Card.Footer>
                    </Card.Link>
                </Card>

                <Card key="5" className={this.state.projectCards[4]}>
                    <Card.Header>Swinging for the Fences</Card.Header>
                    <Card.Body>
                        <Document
                            file={swingingForFences}
                            loading="Loading..."
                            className={this.state.projectDocuments[4]}
                        >
                            <Page pageNumber={this.state.projectPageNums[4]}/>
                        </Document>
                        <Pagination className={this.state.projectDocuments[4]}>
                            <Pagination.Prev onClick={this.renderProjectPage(4, "prev")}/>
                            <Pagination.Next onClick={this.renderProjectPage(4, "next")}/>
                        </Pagination>
                        <Card.Text className={this.state.projectDesc[4]}>
                            A joint research paper to assess the Athletics ability to move to a new stadium. Analysis
                            includes: financial projections, strategic recommendations, and real estate development
                            plan.
                        </Card.Text>
                    </Card.Body>
                    <div onClick={this.renderProjectDocument(4)} className="projectButton">
                        <Card.Footer>
                            Expand Project
                        </Card.Footer>
                    </div>
                    <Card.Link href={swingingForFences}>
                        <Card.Footer>
                            View Project in New Tab
                        </Card.Footer>
                    </Card.Link>
                </Card>
            </CardDeck>
        )
    }

    render() {
        const {
            pageNumber,
            renderResume,
            aboutMeShorten,
            aboutMeExtended
        } = this.state;

        return (
            <div>
                <Jumbotron>
                    <Navbar>
                        <Nav className="mr-auto">
                            {this.renderHeaders()}
                        </Nav>
                    </Navbar>
                    <h1>
                        Carson M Badger
                    </h1>
                </Jumbotron>
                <Container>
                    <Row id="aboutMe" className="rowArea">
                        <Col>
                            <Element name="aboutMe" className="element">
                                About Me
                            </Element>
                            <Col className={aboutMeShorten}>
                                {data.aboutMe.short}
                                <br/>
                                <ButtonToolbar>
                                    <Button href={personal}>
                                        <i className="fas fa-download"></i> Download About Me
                                    </Button>
                                    <Button className={aboutMeShorten + " aboutMeButton"} onClick={this.viewAboutMe}>
                                        <i className="fas fa-angle-down"></i> Expand About Me
                                    </Button>
                                </ButtonToolbar>
                            </Col>
                            <Col className={aboutMeExtended}>
                                <Document
                                    file={personal}
                                    loading="Loading Personal..."
                                    id="aboutMeExpanded"
                                >
                                    <Page pageNumber={this.state.personalPage}/>
                                </Document>
                                <Pagination>
                                    <Pagination.Prev onClick={this.renderAboutMePage("next")}/>
                                    <Pagination.Next onClick={this.renderAboutMePage("prev")}/>
                                </Pagination>
                                <br/>
                                <OverlayTrigger
                                    placement="right"
                                    overlay={
                                        <Tooltip>
                                            Close About Me.
                                        </Tooltip>
                                    }
                                >
                                    <ButtonToolbar>
                                        <Button href={personal}>
                                            <i className="fas fa-download"></i> Download About Me
                                        </Button>
                                        <Button className={aboutMeExtended} onClick={this.viewAboutMe}>
                                            <i className="fas fa-angle-up"></i> Close About Me
                                        </Button>
                                    </ButtonToolbar>
                                </OverlayTrigger>

                            </Col>
                        </Col>
                    </Row>
                    <hr/>
                    <Row id="personal" className="rowArea">
                        <Col>
                            <Element name="personal" className="element">
                                Personal
                            </Element>
                            <Col>
                                Here is some personal stuff.
                            </Col>
                        </Col>
                    </Row>
                    <hr/>
                    <Row id="resume" className="rowArea">
                        <Col>
                            <Element name="resume" className="element">
                                Resume
                            </Element>
                            <Col>
                                <ButtonToolbar>
                                    <Button href={resume}>
                                        <i className="fas fa-download"></i> Download Resume
                                    </Button>
                                    <Button onClick={this.viewResume} className="viewPDF">
                                        <i className="fas fa-expand-arrows-alt"></i> View Resume
                                    </Button>
                                </ButtonToolbar>
                                <br/>
                                <Document
                                    file={resume}
                                    loading="Loading Resume..."
                                    className={renderResume}
                                >
                                    <Page pageNumber={pageNumber}/>
                                </Document>
                            </Col>
                        </Col>
                    </Row>
                    <hr/>
                    <Row id="projects" className="rowArea">
                        <Col>
                            <Element name="projects" className="element">
                                Projects
                            </Element>
                            <br/>
                            <Col>
                                <Container id="projectArea">
                                    <Row className="justify-content-md-center">
                                        {this.renderProjects()}
                                    </Row>
                                </Container>
                            </Col>

                        </Col>
                    </Row>
                    <hr/>
                    <Row id="aboutMe" className="rowArea">
                        <Col>
                            <Element name="contact" className="element">
                                Contact Me
                            </Element>
                            <Col>
                                <ListGroup variant="flush">
                                    <ListGroup.Item>
                                        <i className="far fa-envelope"></i> Email:
                                        <ol>
                                            <li>
                                                Personal: <Mailto email="carsonbadger@yahoo.com"/>
                                            </li>
                                            <li>
                                                School <Mailto email="cbadger@scu.edu"/>
                                            </li>
                                        </ol>
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <i className="fab fa-linkedin"></i> LinkedIn: <a
                                        href="https://www.linkedin.com/in/carson-badger-51ba9a89/">linkedin.com/in/carson</a>
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <i className="fab fa-github"></i> Github: <a
                                        href="https://github.com/carsonbadger">github.com/carsonbadger</a>
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                        <i className="fas fa-phone"></i> Phone: <Mailto tel="650-208-8203"/>
                                    </ListGroup.Item>
                                </ListGroup>
                            </Col>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default App;
